body{
    background-color: black;
    color:white;
    margin: 4px;

}
.logo{

    width: 147px;
    height: 151px;
    position: fixed;
    top: -100px;
    left: 34px;
    overflow: hidden;
}

    

.about-nav{
    margin-left: 300px;

}
.slogo{
    position: absolute;
    width: 486px;
    border: 2px solid red;
    bottom: 56px;
    left: -140px;
    height: 128px;
    top: -43px;
}


.main-nav{
    background: rgba(50, 253, 224, 0);
    border-radius: 4px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(8.2px);
    -webkit-backdrop-filter: blur(8.2px);
    border: 1px solid rgba(28, 119, 105, 0.218);
    position: sticky;
}
.main-nav ul{
    display: flex;
    margin-left: 150px;
    

}
.left-nav{
    list-style: none;
    margin-left: 25px;
}
.Search-and-contact{
    margin-left: auto;
    margin-right: 10px;
    list-style: none;
}
.Search-and-contact .right{
    margin-right: 16px;
}


.main-nav spline-viewer {
    position: absolute;
    top: 32px;
    left: 5px;
    width: 113px;
    height: 122%;
    z-index: 1;
}
nav li a, nav ul a{
    text-decoration: none;
    color:white;
}

.conatct-btn{
    background-color: black;
    color: black;
    position: relative;
    /* height: 23px; */
    width: 120px;
    border-style: none;
    border-radius: 10px;
    

}
:root{
    --blurval:0;
    --padval:1px;
    --degr:45deg;
}
.conatct-btn::after{
    content: ''; 
    position:absolute;
    width: 100%;
    height: 100%;
    translate: -50% -50%;
    left: 50%;
    top:50%;
    z-index: -1;
    padding:var(--padval);
    background-image: linear-gradient(var(--degr),#0E8787,rgba(62, 63, 63, 0.485));
    border-radius: 10px;
    filter: blur(var(--blurval));
    
    
   

}

.conatct-btn:hover{
    animation: 1s blureff step-start infinite;
  
}


.Search-and-contact input::-webkit-input-placeholder{
    color:rgb(175, 175, 175);
}

.search {
    background-color: transparent;
    position: relative;
    border-radius: 10px;
    color: white;
    
}
.search::after{
    content: '';
    position: absolute;
    height: 100%;
    width: 100%;
    translate: -50% -50%;
    left: 50%;
    top:50%;
    padding: 2px;
    background-image: linear-gradient(var(--degr),#0E8787,rgba(62, 63, 63, 0.485));
    border-radius: 10px;
}

.solution #solution{
    background-color: transparent;
    color: white;
}
.solution #solution option{
    background-color: transparent;
    color:black;
}


@keyframes blureff{
    from{
        --blurval:0px;

    }
    to{
        --blurval:10px;
        --padval:2px;
    }
}