.Download-cv-button{
    margin-top:50px;
    background-color: rgb(0, 0, 0);
    background: linear-gradient(#000000, #000000), 
    linear-gradient(to right, rgb(17, 17, 17), #256464);
    background-origin: padding-box, border-box;
    background-repeat: no-repeat; /* this is important */
    border: 1px solid transparent;
    color:white;
    border-radius: 5px;
    padding: 5px;
}

body{
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    scrollbar-width:0px;
    
}
html{
    overflow-x: hidden;
    scroll-behavior: smooth;
    scrollbar-width:0px;

}